
import wx from 'weixin-js-sdk'
import empty from '../components/empty'
import {mapGetters,mapActions} from 'vuex'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
data(){
return{
dataList:[],
flag:false,
sy:0,
ey:0,
max:0,
onFetching:false,
tips:''
}
},
created(){

if(this.$route.query.token){
localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
}
if(this.$route.query.refreshToken){
localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
}
this.showLoading()
this.returnList({this:this,data:{viewIndex:0,viewSize:10},method:this.returnList})
},
computed:{
},
components:{
empty ,LoadMore
},
methods:{
...mapActions(['returnList']),
ontouchstart(e){
this.sy = e.changedTouches[0].pageY
},
ontouchend(e){
this.ey = e.changedTouches[0].pageY
if(this.sy - this.ey > 30){
if(!this.onFetching){
this.bindScroll()
}
}
},
bindScroll(){
let no = this.dataList.length
let to = this.max
if(no >= to){
this.tips='没有更多了'
this.onFetching = true
return
}
let app = this.$refs.outer
let dh = app.scrollTop
let h = app.clientHeight
let bh = this.$refs.content.clientHeight
if(bh - dh < h ){

this.onFetching = true
this.tips='正在加载'
this.returnList({this:this,data:{viewIndex:parseInt(this.dataList.length/10),viewSize:10},method:this.returnList,type:'adm'})

}
},
goToIndex(){
wx.miniProgram.switchTab({
url: '/pages/home/index'
})
},
toSe(list){
console.log(list)



this.$router.push('/reviewProgress?returnId='+list.returnId+'&id='+list.orderId)
},
getData(data){
this.flag = true
this.max = data.data.max
if(data.type==='adm'){
this.dataList.push(...data.data.returnList)
}
else{
this.dataList = data.data.returnList
}

},
choseWhy(k){
console.log(k);
}
},
mounted(){
document.title = '退换售后'
},
filters:{
formTime(c){
if(!c){
return ''
}
let time =  new Date(c)
let Y= time.getFullYear()
let M= time.getMonth() +1 <10? '0'+ (time.getMonth()+1):time.getMonth() +1
let D= time.getDate()
let h= time.getHours()
let m= time.getMinutes()
let s= time.getSeconds()
return Y+'-'+M+'-'+D+' '+h+':'+m+':'+s
},
fiState(s){
let d=''
switch (s){
case 'RETURN_WAITEXAMINE':
d= '待审核'
break;
case 'RETURN_WAITFEFUND':
d= '待退款'
break;
case 'RETURN_COMPLETED':
d= '已完成'
break;
case 'RETURN_REJECTAPPLY':
d= '已拒绝'
break;
}
return d
}
},
beforeRouteLeave(to,from,next){
console.log(to)
if(to.name==='reviewProgress'){
next()
}
else{
wx.miniProgram.switchTab({
url:'/pages/me/index'
})
}
}
}



